import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loggedIn: false
  },
  mutations: {
    loggedOut() {
      localStorage.clear('token');
    }
  },
  actions: {
    loggingOut(context) {
      context.commit('loggedOut')
    },
  },
  modules: {
  }
})
